/**
 * Add font scale with media query as per configuration on $body-text
 * Ex: @include body-text( h1 );
 * This will add font-size and line-height with different breakpoints.
 */
.fwp-button {
  --bg-button-primary: #283fcd;
  --bg-button-secondary: #2271b1;
  display: inline-block;
  font-weight: 500;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  color: #fff;
  border: 0;
  text-decoration: none;
  background: var(--bg-button-primary);
  padding: 0.4375rem 0.75rem;
  font-size: 0.8125rem;
  line-height: 0.6575rem;
  border-radius: 3px;
  transition: background-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}
.fwp-button:focus {
  outline: unset;
  box-shadow: none;
}
.fwp-button:hover {
  color: #fff;
  text-decoration: none;
  background: var(--bg-button-secondary);
}
.fwp-button:not(:disabled):not(.disabled) {
  cursor: pointer;
}
.fwp-button__outline {
  color: var(--bg-button-primary);
  background: none;
  border: 1px solid;
}
.fwp-button__outline:hover {
  color: var(--bg-button-secondary);
  background: rgba(147, 0, 60, 0.04);
}
.fwp-button span {
  position: relative;
  display: block;
  top: 2px;
}
.fwp-button.fwp-btn-icononly {
  position: relative;
  padding: 5px;
  border: 0.2px solid var(--bg-button-primary);
  background: none;
  color: var(--bg-button-primary);
}
.fwp-button.fwp-btn-icononly:before {
  width: 20px;
  height: 20px;
  speak: never;
  line-height: 1;
  font-size: 20px;
  font-weight: 400;
  font-style: normal;
  text-align: center;
  vertical-align: top;
  text-transform: none;
  text-rendering: auto;
  display: inline-block;
  font-family: dashicons;
  text-decoration: inherit;
  transition: color 0.1s ease-in;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.fwp-button.fwp-btn-icononly.on:before, .fwp-button.fwp-btn-icononly[data-status=on]:before {
  content: "\f15e";
}
.fwp-button.fwp-btn-icononly.off:before, .fwp-button.fwp-btn-icononly[data-status=off]:before {
  content: "\f158";
}
.fwp-button.fwp-btn-icononly.loading, .fwp-button.fwp-btn-icononly[data-status=loading] {
  background: none;
  height: 25px;
  width: 25px;
  display: inline-block;
  position: relative;
}
.fwp-button.fwp-btn-icononly.loading:before, .fwp-button.fwp-btn-icononly[data-status=loading]:before {
  content: "";
  width: 15px;
  height: 15px;
  border-radius: 50%;
  border: 5px solid #e1dada;
  animation: fwp-anim-spin 1s linear infinite;
  border-top: 5px solid var(--bg-button-primary);
}

.btn {
  --card-btn-padding-x: 1.5rem;
  --card-btn-padding-y: 0.5rem;
  --card-btn-font-family: ;
  --card-btn-font-size: 1rem;
  --card-btn-font-weight: 400;
  --card-btn-line-height: 1.5;
  --card-btn-color: #6D6D6D;
  --card-btn-bg: transparent;
  --card-position: relative;
  --card-btn-border-width: 1px;
  --card-btn-border-color: transparent;
  --card-btn-border-radius: 0.25rem;
  --card-btn-box-shadow: 0 0px 0px 0 rgba(0, 0, 0, 0);
  --card-btn-disabled-opacity: 0.65;
  --card-btn-focus-box-shadow: 0 0 0 0rem rgba(var(--card-btn-focus-shadow-rgb), .5);
  display: inline-block;
  padding: var(--card-btn-padding-y) var(--card-btn-padding-x);
  font-family: var(--card-btn-font-family);
  font-size: var(--card-btn-font-size);
  font-weight: var(--card-btn-font-weight);
  line-height: var(--card-btn-line-height);
  color: var(--card-btn-color);
  text-align: center;
  position: var(--card-position);
  vertical-align: middle;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border: var(--card-btn-border-width) solid var(--card-btn-border-color);
  -webkit-border-radius: var(--card-btn-border-radius);
  border-radius: var(--card-btn-border-radius);
  background-color: var(--card-btn-bg);
  -webkit-box-shadow: var(--card-btn-box-shadow);
  box-shadow: var(--card-btn-box-shadow);
  -webkit-transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, -webkit-box-shadow 0.15s ease-in-out;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, -webkit-box-shadow 0.15s ease-in-out;
  -o-transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out, -webkit-box-shadow 0.15s ease-in-out;
}

