.animated {
  animation-duration: 1s;
  animation-fill-mode: both;
  animation-timing-function: ease-out;
}

/* stylelint-disable */
@keyframes fwp-anim-fadeInLeft {
  0% {
    opacity: 0;
    transform: translateX(-20px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
.fwp-anim-fadeInLeft {
  animation-name: fwp-anim-fadeInLeft;
}

@keyframes fwp-anim-fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
.fwp-anim-fadeInUp {
  animation-name: fwp-anim-fadeInUp;
}

@keyframes fwp-anim-fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.fwp-anim-fadeIn {
  animation-name: fwp-anim-fadeIn;
}

@keyframes fwp-anim-animateIn {
  0% {
    opacity: 0;
    transform: scale(0.6) translateY(-8px);
  }
  100% {
    opacity: 1;
  }
}
.fwp-anim-animateIn {
  animation-name: fwp-anim-fadeIn;
}

@keyframes fwp-anim-spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.fwp-anim-spin {
  animation-name: fwp-anim-spin;
}

/* stylelint-enable */
.sb {
  display: flex;
  width: 100%;
  background: #000;
  color: #fff;
  max-height: 100vh;
  height: 100vh;
  min-height: 600px;
  gap: 10px;
  justify-content: space-between;
}
.sb-l, .sb-r {
  width: 20%;
  border: none;
}
.sb-l {
  border-right: 1px solid #eee;
}
.sb-r {
  border-left: 1px solid #eee;
}
.sb-c {
  width: 60%;
  padding: 20px;
  position: relative;
}
.sb-c-wb {
  padding: 15px;
  border: 1px solid #ddd;
  border-radius: 7px;
}
.sb-c-wb-pre {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.sb-c-wb-r {
  gap: 20px;
  display: flex;
  flex-direction: column;
}
.sb-c-wb .ct-rw {
  gap: 10px;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
}
.sb-c-wb .ct-av {
  width: 50px;
  height: auto;
  display: flex;
  min-width: 50px;
  align-items: center;
  justify-content: center;
}
.sb-c-wb .ct-av-img {
  width: 100%;
  height: auto;
}
.sb-c-wb .ct-msg-blk {
  font-size: 20px;
  width: calc(100% - 100px);
}
.sb-c-wb .ct-msg-acts {
  gap: 7px;
  display: flex;
  justify-content: center;
}
.sb-c-wb .ct-msg-acts > * {
  width: 20px;
  height: 20px;
  display: block;
  background: #ddd;
}
.sb-c-wb .ct-blk {
  padding: 20px;
  transition: all 0.7s ease;
}
.sb-c-wb .ct-blk:hover {
  color: #333;
  background: #eee;
}
.sb-c-wb .ct-blk-chunks {
  pointer-events: none;
  transition: all 0.7s ease;
}
.sb-c-wb .ct-blk-chunks .ct-msg-blk {
  position: relative;
}
.sb-c-wb .ct-blk-chunks .ct-msg-blk:after {
  content: ".";
  -webkit-animation: loading-dots 3s infinite;
  animation: loading-dots 3s infinite;
}
.sb-c-wf {
  width: 100%;
  padding: 20px;
}
.sb-c-wf-b {
  color: #eee;
  background: none;
  padding: 10px 20px;
  border: 1px solid #eee;
}

/* Skeleton Text Effect */
@-webkit-keyframes loading-dots {
  0% {
    content: ".";
  }
  50% {
    content: "..";
  }
  100% {
    content: "...";
  }
}
@keyframes loading-dots {
  0% {
    content: ".";
  }
  50% {
    content: "..";
  }
  100% {
    content: "...";
  }
}